.nav{
    position: fixed;
    top: 0;
    width: 100%;
    height: 25px;
    padding: 20px;
    transition-timing-function:  ease-in;
    transition: all 0.5s;
}

.nav_black {
    background-color: #111;
}
.nav_logo{
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}

.nav_avatar{
    position: fixed;
    right: 20px;
    width: 60px;
    object-fit: contain;
}